/*===KEYFRAMES===*/
@-webkit-keyframes complete-big {
  0% {
    stroke-dashoffset: 200;
    
  }
  50% {
    stroke-dashoffset: 1100;
    
  }
  100% {
    stroke-dashoffset: 200;
    
  }
}
@keyframes complete-big {
  0% {
    stroke-dashoffset: 200;
    
  }
  50% {
    stroke-dashoffset: 1100;
    
  }
  100% {
    stroke-dashoffset: 200;
    
  }
}
@-webkit-keyframes rotate-big {
  0% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}
@keyframes rotate-big {
  0% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}
@-webkit-keyframes complete-small {
  0% {
    stroke-dashoffset: 800;
    
  }
  50% {
    stroke-dashoffset: 100;
    
  }
  100% {
    stroke-dashoffset: 800;
    
  }
}
@keyframes complete-small {
  0% {
    stroke-dashoffset: 800;
    
  }
  50% {
    stroke-dashoffset: 100;
    
  }
  100% {
    stroke-dashoffset: 800;
    
  }
}
@-webkit-keyframes rotate-small {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate-small {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/*===DEFAULT_STYLING===*/
* {
  box-sizing: border-box;
}

body{
  font-family: 'Lato', sans-serif;
  margin: 0;
  font-size: 12px;
  box-sizing: border-box;
}

#root{
  z-index: 9999999;
}

a{
  display: block;
}

div.navigation-wrapper{
  position: fixed;
  top: 0px;
}

div.main-wrapper{
  position: relative;
  width: 1000px;
  margin: 60px auto 0px auto;
  min-height: 500px;
}

.modal{
  background-color: aliceblue;
  border: solid 2px black;
  border-radius: 15px;
}

.light-modal{
  border: solid 1px #c4c4c4;
  border-radius: 15px;
}

a{
  color: black;
  text-decoration: none;
  cursor: pointer;
}

button{
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

button:disabled{
  cursor: not-allowed;
}

textarea:focus, input:focus, a:focus, div:focus{
  outline: none;
}

input[type=text]{
  box-sizing: border-box;
  background-color: transparent;
  border: 0px;
  border-bottom: solid 1px black;
  padding: 3px 5px 3px 5px;
  color: black;
}

div.title{
  z-index: 99999999;
  position: fixed;
  top: 0px;
  left: 100px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-size: 21px;
}

/*checkboxes*/
input[type=checkbox]{
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0;
}
.checkmark {
  display: inline-block;
  margin: 6px;
  height: 20px;
  width: 20px;
  background-color:white;
  border-radius: 3px;
  border: solid 0.5px gray;
}
.checkbox-container{
  cursor: pointer;
}
.checkbox-container:hover input ~ .checkmark {
  background-color: rgb(241, 241, 241);
}
.checkmark:after {
  content: url(/slr-web/static/media/tick.df0b2afe.svg);
  display: none;
}
.checkbox-container input:checked ~ .checkmark:after {
  display: block;
}
.checkbox-container .checkmark:after {
  margin-left: 2px;
  margin-top: 2px;
  width: 14px;
  height: 14px;
}

/*loading icon*/
svg#loading-icon{
  position: absolute;
  width: 80px;
  height: 80px;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}
svg#loading-icon path#loading-circle1{
  fill: transparent;
  stroke: black;
  stroke-width: 3px;
  stroke-dashoffset: 1005;
  stroke-dasharray: 1005; /*1005*/
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-animation: complete-small 6s linear infinite, rotate-small 1s linear infinite;
          animation: complete-small 6s linear infinite, rotate-small 1s linear infinite;
}
svg#loading-icon path#loading-circle2{
  fill: transparent;
  stroke: black;
  stroke-width: 9px;
  stroke-dashoffset: 0;
  stroke-dasharray: 1917;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-animation: complete-big 5s linear infinite, rotate-big 1s linear infinite;
          animation: complete-big 5s linear infinite, rotate-big 1s linear infinite;
}

/*buttons*/
button.bottom-left-btn{
  font-weight: 300;
  font-size: 50px;
  position: fixed;
  bottom: 20px;
  right: 40px;
}

/*code tag*/
code{
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

